import React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Toolbar from "@mui/material/Toolbar";
import Container from "@mui/material/Container";
import { NavLink as Link } from "react-router-dom";
import Button from "@mui/material/Button";


import ScrollToTop from "./Others/ScrollToTop";
import DarkMode from "./Others/DarkMode";

/* ICONS */
import IconButton from '@mui/material/IconButton';
import MenuIcon from "@mui/icons-material/Menu";

/* LOGO */
import logo from "../images/logo.svg";

const Header = () => {

  const navigation = [
    {
      id: 1,
      navItem: "Home",
      to: "/",
      value: "home",
    },
    {
      id: 2,
      navItem: "About",
      to: "/About",
      value: "about",
    },
    {
      id: 3,
      navItem: "Objectives",
      to: "/Objectives",
      value: "objectives",
    },
    {
      id: 4,
      navItem: "Education",
      to: "/Education",
      value: "education",
    },
    {
      id: 5,
      navItem: "Skills",
      to: "/Skills",
      value: "skills",
    },
    {
      id: 6,
      navItem: "Projects",
      to: "/Projects",
      value: "projects",
    },
    {
      id: 7,
      navItem: "Experiences",
      to: "/Experiences",
      value: "experiences",
    },
    {
      id: 8,
      navItem: "Contact",
      to: "/Contact",
      value: "contact",
    },
  ];

  const [mobileOpen, setMobileOpen] = React.useState(false);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const scrollTop = () => {
    window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
  };

  //DRAWER
  const drawer = (
    <Container>
      <Box onClick={handleDrawerToggle} className="main-box">
        <Button className="btn-home" onClick={scrollTop} disableRipple>
          <Link to="/">
            <img src={logo} className="logo" alt="logo" />
          </Link>
        </Button>
        {/* NAVIGATION */}
        <Box className="item-box">
          {navigation.map((item) => (
            <Button key={item.id} onClick={scrollTop} disableRipple>
              <Link to={item.to} className="nav-link" value={item.value}>
                {item.navItem}
              </Link>
            </Button>
          ))}
        </Box>
        <DarkMode/>
      </Box>
    </Container>
  );

  return (
    <header>
      <Box className="main-box">
        <AppBar component="nav">
          <Container>
            <Toolbar>
              {/* ICON BUTTON */}
              <div className="menu-btn">
                <IconButton
                  disableRipple={false}
                  aria-label="open drawer"
                  size="large"
                  edge="end"
                  onClick={handleDrawerToggle}
                >
                  <MenuIcon className="menu-icon"/>
                </IconButton>
              </div>

              {/* LOGO IMAGE */}
              <Button className="btn-home" onClick={scrollTop} disableRipple>
                <Link to="/">
                  <img src={logo} className="logo" alt="logo" />
                </Link>
              </Button>

              {/* NAVIGATION */}
              <Box className="item-box">
                {navigation.map((item) => (
                  <Button key={item.id} onClick={scrollTop} disableRipple>
                    <Link to={item.to} className="nav-link" value={item.value}>
                      {item.navItem}
                    </Link>
                  </Button>
                ))}
              </Box>
              <DarkMode class="dark-mode-desktop"/>
            </Toolbar>
          </Container>
        </AppBar>

        <Box component="nav">
          <Drawer
            className="drawer"
            variant="temporary"
            anchor="right"
            open={mobileOpen}
            color="inherit"
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true,
            }}
          >
            {drawer}
          </Drawer>
        </Box>
      </Box>
      <ScrollToTop/>
    </header>
  );
};

export default Header;
