import * as React from "react";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(4),
}));

const SharedBox = (props) => {
    return (
      <Container className="container project">
        <Grid container spacing={2}>
          <Grid item md={2}></Grid>
          <Grid item md={8}>
            <div className="main-title">{props.name}</div>
          </Grid>
        </Grid>

        <Box sx={{ borderColor: "secondary.main" }} className="box">
          <Grid container spacing={2} className="container">
            <Grid item lg={2} md={1}></Grid>
            <Grid item lg={8} md={10} sm={12} xs={12} pb={5} className="children">
              <Item className="item"> {props.children} </Item>
            </Grid>
            <Grid item lg={2}md={1}></Grid>
          </Grid>
        </Box>
      </Container>
    );
  }




export default SharedBox;
