import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import { useForm, Controller } from "react-hook-form";
import PhoneInput, {
  parsePhoneNumber,
  getCountryCallingCode,
} from "react-phone-number-input";
import ScrollAnimation from "react-animate-on-scroll";

import SendRoundedIcon from "@mui/icons-material/SendRounded";

const Contact = () => {
  const {
    register,
    handleSubmit,
    control,
    reset,
    formState: { errors },
  } = useForm();

  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [email, setEmail] = useState();
  const [newMessage, setNewMessage] = useState();
  const [companyName, setCompanyName] = useState();
  const [position, setPosition] = useState();
  const [countryCode, setCountryCode] = useState("LB");

  const registerOptions = {
    First_Name: { required: "First name is required." },
    Last_Name: { required: "Last name is required." },
    Email_Address: { required: "Email is required." },
    Text_Message: { required: "Please enter your message!" },
    Company_Name: { required: "Company name is required." },
    Position: { required: "Position is required." },
  };
  const handleRegistration = (data) => {
    console.log(data);
    const url = `https://wa.me/+96179131421?text=${JSON.stringify(data)
      .split(/["{}_ ]+/)
      .join("%20")}`;
    const newUrl = url.split(",").join("%0A");
    window.location.href = newUrl;
    reset();
  };

  return (
    <>
      <ScrollAnimation animateIn="zoomIn">
        <form onSubmit={handleSubmit(handleRegistration)}>
          <Grid container spacing={2} py={1}>
            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                id="outlined-basic"
                variant="outlined"
                placeholder="First Name"
                name="First_Name"
                type="text"
                value={firstName}
                onChange={(e) => setFirstName(e.target.value)}
                {...register("First_Name", registerOptions.First_Name, {
                  required: true,
                })}
              />

              <div className="error">
                {errors?.First_Name && errors.First_Name.message}
              </div>
            </Grid>

            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                id="outlined-basic"
                variant="outlined"
                placeholder="Last Name"
                name="Last_Name"
                type="text"
                value={lastName}
                onChange={(e) => setLastName(e.target.value)}
                {...register("Last_Name", registerOptions.Last_Name, {
                  required: true,
                })}
              />
              <div className="error">
                {errors?.Last_Name && errors.Last_Name.message}
              </div>
            </Grid>

            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                name="Email_Address"
                id="outlined-basic"
                variant="outlined"
                placeholder="Email Address"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                {...register("Email_Address", registerOptions.Email_Address, {
                  required: true,
                })}
              />
              <div className="error">
                {errors?.Email_Address && errors.Email_Address.message}
              </div>
            </Grid>

            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                name="Company_Name"
                id="outlined-basic"
                variant="outlined"
                placeholder="Company Name"
                type="text"
                value={companyName}
                onChange={(e) => setCompanyName(e.target.value)}
                {...register("Company_Name", registerOptions.Company_Name, {
                  required: true,
                })}
              />
              <div className="error">
                {errors?.Company_Name && errors.Company_Name.message}
              </div>
            </Grid>

            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                name="Position"
                id="outlined-basic"
                variant="outlined"
                placeholder="Position"
                type="text"
                value={position}
                onChange={(e) => setPosition(e.target.value)}
                {...register("Position", registerOptions.Position, {
                  required: true,
                })}
              />
              <div className="error">
                {errors?.Position && errors.Position.message}
              </div>
            </Grid>

            <Grid item lg={6} md={12} sm={12} xs={12} py={2} className="col">
              <Controller
                name="Phone_Number"
                rules={{
                  validate: {
                    isValid: (value) => {
                      if (value) {
                        const callingCode = getCountryCallingCode(countryCode);
                        if (!new RegExp(`^\\+${callingCode}$`).test(value)) {
                          return !!parsePhoneNumber(value);
                        }
                      }
                      return true;
                    },
                  },
                }}
                control={control}
                render={({ field }) => (
                  <PhoneInput
                    {...field}
                    smartCaret
                    onCountryChange={(v) => setCountryCode(v)}
                    limitMaxLength={true}
                    international={true}
                    rules={{ required: true }}
                    defaultCountry="LB"
                  />
                )}
              />
              {errors.Phone_Number?.type === "isValid" && (
                <span className="validation-message">
                  Enter a valid phone number.
                </span>
              )}
            </Grid>

            <Grid item lg={12} md={12} sm={12} xs={12} py={2} className="col">
              <TextField
                id="outlined-basic"
                variant="outlined"
                placeholder="Message"
                multiline={true}
                name="Text_Message"
                maxRows="5"
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                {...register("Text_Message", registerOptions.Text_Message, {
                  required: true,
                })}
              />
              <div className="error">
                {errors?.Text_Message && errors.Text_Message.message}
              </div>
            </Grid>

            <Grid
              item
              lg={12}
              md={12}
              sm={12}
              xs={12}
              py={2}
              className="button-col"
            >
              <Button
                type="submit"
                size="large"
                variant="contained"
                color="inherit"
              >
                <SendRoundedIcon /> &nbsp;SEND
              </Button>
            </Grid>
          </Grid>
        </form>
      </ScrollAnimation>
    </>
  );
};
export default Contact;
